.prices .container .center { margin-top: 50px; text-align: center; }
.prices .container .center .button.button--aylen { background: #ff6600; font-size: 24px; text-transform: uppercase; color: #fff; font-family: sofiapro-light; font-weight: 400; padding: 15px 56px; cursor: pointer; }
.prices .container .center .button.button--aylen:hover { background: transparent; }

